<template>
  <div>
    <a class="card" :style="[getStyle]" @click="go()">
      <CRow>
        <!-- <CCol md="2">
          <CImg :src="icon" width="32px" height="32px"></CImg>
        </CCol> -->
        <CCol md="12" class="text-center">
          <h3>{{ name }}</h3>
        </CCol>
      </CRow>
      <CRow class="pt-5">
        <CCol md="5">
          <h5><font-awesome-icon icon="tachometer-alt"/> 設定速度:</h5>
        </CCol>
        <CCol md="7" class="text-center">
          <h5><div style="background:#e8fffe;padding:4px;color:#04073b;">{{ speedSet }}</div></h5>
        </CCol>
      </CRow>
      <CRow>
        <CCol md="5">
          <h5><font-awesome-icon icon="tachometer-alt"/> 目前速度:</h5>
        </CCol>
        <CCol md="7" class="text-center">
          <h5><div style="background:#e8fffe;padding:4px;color:#04073b;">{{ speedCurrent }}</div></h5>
        </CCol>
      </CRow>
      <CRow>
        <CCol md="5">
          <h5><font-awesome-icon icon="rainbow"/> 目前米數:</h5>
        </CCol>
        <CCol md="7" class="text-center">
          <h5><div style="background:#e8fffe;padding:4px;color:#04073b;">{{ meters }}</div></h5>
        </CCol>
      </CRow>
      <CRow>
        <CCol md="5">
          <h5><font-awesome-icon icon="power-off"/> 機台啟動:</h5>
        </CCol>
        <CCol md="7" class="text-center">
          <h5 v-if="status===0"><div style="background:#63F53F;padding:4px;color:#04073b;">啟動</div></h5>
          <h5 v-else><div style="background:#bbbfbf;padding:4px;color:#04073b;">停止</div></h5>
        </CCol>
      </CRow>
      <hr>
      <CRow>
        <CCol v-if="error"><h5 class="text-center">{{errMsg}}</h5></CCol>
        <CCol v-else><h5 style="visibility:hidden;">Space</h5></CCol>
      </CRow>
      <div class="go-corner" href="#">
        <div class="go-arrow">
          <!-- → -->
        </div>
      </div>
    </a>
  </div>
</template>

<script>
import icon from '../../../public/img/brand/production.png'


export default {
  props: [ 'mac', 'name', 'line_id' ],
  data() {
    return {
      icon: icon,
      timer: null,
      speedSet: '',       // 設定速度
      speedCurrent: '',   // 目前數度
      meters: '',         // 目前米數
      status: 1,          // 0:啟動, 1:停機
      error: 0,           // 0: 無異常, 1: 有異常
      errMsg: '',
    }
  },
  created() {
    this.getData()
    this.timer = setInterval(() => {
      this.getData()
    }, 2000)
  },
  beforeDestroy() {
    clearInterval(this.timer)
  },
  computed: {
    getStyle() {
      if(this.error===0) {  // 無異常
        return {'background-image': 'linear-gradient(-225deg, #41e595 0%, #25c0b5 100%)'}   // green
      } else {              // 有異常
        return {'background-image': 'linear-gradient(-225deg, #fa9793 0%, #e33b3b 100%)'}    // red
      }
    },
  },
  methods: {
    getData() {
      this.$http.get(`/api/FormingLastData/${this.mac}`)
      .then(res => {
        if(res.data.length>0) {
          this.speedSet = res.data[0].R120
          this.speedCurrent = res.data[0].R130
          this.meters = res.data[0].R110
          this.status = res.data[0].M666
          
          // check exception
          if(res.data[0].arrError.length===0) {
            this.error = 0
          } else {
            this.error = 1
            this.errMsg = ''
            for(let i=0; i<res.data[0].arrError.length; i++) {
              this.errMsg += res.data[0].arrError[i].msg
              if(i<res.data.length-1) {
                this.errMsg += '/'
              } 
            }
          }
        } else {
          this.speedSet = '--'
          this.speedCurrent = '--'
          this.meters = '--'
        }
      })
      .catch(err => { console.log(err.response.data.message) })
    },
    go() {
      this.$store.commit('setLine', {line_id: this.line_id, line_name: '', equip_mac: this.mac, equip_name: this.name, plc_ip: ''})
      this.$router.push('/equipment/forming/' + this.mac)
    }
  }
}
</script>

<style lang="scss" scoped>
.go-corner {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 32px;
  height: 32px;
  overflow: hidden;
  top: 0;
  right: 0;
  background-color: #00838d;
  border-radius: 0 4px 0 32px;
}

.go-arrow {
  margin-top: -4px;
  margin-right: -4px;
  color: white;
  font-family: courier, sans;
}

.card {
  display: block;
  top: 0px;
  position: relative;
  // max-width: 262px;
  // background-color: #ffffff;
  // background-image: linear-gradient(45deg, #92D000 0%, #7AC493 100%);
  color: white;
  border-radius: 4px;
  box-shadow: 20px 20px 15px 0 rgba(0,0,0,0.125);
  transition: box-shadow 0.25s ease-in-out;
  padding: 32px 24px;
  margin: 12px;
  text-decoration: none;
  overflow: hidden;
  border: 1px solid #cccccc;
  cursor: pointer;
  
  .go-corner {
    background-color: #00838d;
    height: 100%;
    width: 16px;
    padding-right: 10px;
    border-radius: 0;
    transform: skew(4deg);
    margin-right: -36px;
    align-items: start;
    // background-image: linear-gradient(-45deg, #8F479A 1%, #DC2A74 100%);
    // background-image: linear-gradient(-45deg, #8F479A 1%, #4DB4E8 100%);
    background-image: linear-gradient(to top, #37ecba 0%, #72afd3 100%);
  }
  
  .go-arrow {
    transform: skew(-5deg);
    margin-left: -2px;
    margin-top: 9px;
    opacity: 0;
  }

  // .go-corner {
  //   margin-right: -12px;
  // }
  // .go-arrow {
  //   opacity: 1;
  // }

  &:hover {
    border: 1px solid #CD3D73;

    transition: all 0.2s ease-out;
    box-shadow: 0px 4px 8px rgba(38, 38, 38, 0.2);
    top: -4px;
    border: 1px solid #cccccc;
    background-color: white;
  }
  
  // h3 {margin-top: 8px;}
}

.card:hover {
  .go-corner {
    margin-right: -12px;
  }
  .go-arrow {
    opacity: 1;
  }
}

.mydiv {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
}


</style>


