<template>
  <div class="pt-3">
    <CRow :gutters="false" class="justify-content-center">
      <CCol md="12" sm="12">
        <CButton shape="pill" color="info" variant="ghost" class="float-right"><font-awesome-icon icon="cog"/></CButton>
        <CButton shape="pill" color="info" variant="ghost" class="float-right"><font-awesome-icon icon="plus"/></CButton>
      </CCol>

      <CCol md="3" sm="6">
        <Forming v-if="loaded" :mac="equipForming[0].mac" :name="equipForming[0].name" :line_id="equipForming[0].line_id"></Forming>
      </CCol>
      <CCol md="3" sm="6">
        <Drawing v-if="loaded" :mac="equipDrawing[0].mac" :name="equipDrawing[0].name" :line_id="equipDrawing[0].line_id"></Drawing>
      </CCol>
      <CCol md="3" sm="6">
        <Spooling v-if="loaded" :mac="equipSpooling[0].mac" :name="equipSpooling[0].name" :line_id="equipSpooling[0].line_id"></Spooling>
      </CCol>
      
    </CRow>
    
  </div>
</template>

<script>
import Forming from './card/cardForming'
import Drawing from './card/cardDrawing'
import Spooling from './card/cardSpooling'

export default {
  components: { Drawing, Forming, Spooling },
  data() {
    return {
      loaded: false,
      equipDrawing: [],     // 細伸線機
      equipForming: [],     // 成型機
      equipSpooling: [],    // 分捲機
    }
  },
  mounted() {
    this.loaded = false
    this.$http.get('/api/Equipments')
    .then(res => {
      this.equipDrawing = []
      this.equipForming = []
      this.equipSpooling = []
      for(let i=0; i<res.data.length; i++) {
        let equip = {
          mac: res.data[i].mac,
          name: res.data[i].name,
          line_id: res.data[i].line_id
        }
        switch (res.data[i].type) {
          case 'Forming':
            this.equipForming.push(equip)
            break
          case 'Drawing':
            this.equipDrawing.push(equip)
            break
          case 'Spooling':
            this.equipSpooling.push(equip)
            break
          default:
            alert(`沒有符合的設備類型: ${res.data[i].type}`)
        }
      }
      this.loaded = true
    })
    .catch(err => { alert(err.response.data.message) })
  },
  methods: {
    
  }
}
</script>

